<script setup>
import { Handle, Position } from '@braks/vue-flow';
import { VueLatex } from 'vatex';
import { onMounted, ref, watch } from 'vue';
let props = defineProps({
  data: {
    type: Object,
    required: true
  }
})
let ch, zn;
let fnc = ref('');
onMounted(() => {
  fnc.value = makeFunction();
})
watch(() => props.data.data.params.ch, (value) => {//отслеживаем изменение числителя
  fnc.value = makeFunction();
})
watch(() => props.data.data.params.zn, (value) => {//отслеживаем изменение знаментателя
  fnc.value = makeFunction();
})
const makeFunction = () => {
  return String('frac{' + makeCh() + '}{' + makeZn() + '}');
}
const makeCh = () => {
  ch = props.data.data.params.ch.split(' ')
  let out = '';
  for (let i = 0; i < ch.length; i++) {
    if (ch[i][0] != '-' && i != 0) {
      ch[i] = '+' + ch[i];
    }
    let exp = ch.length - 1 - i;
    if (exp == 0) {
      out += ch[i];
    }
    if (ch[i] == '1') {
        ch[i] = '';
      }
    if (exp == 1) {
      if (ch[i] == '1') {
        ch[i] = '';
      }
      out += ch[i] + 'S';
    }
    if (exp > 1) {
      if (ch[i] == '1') {
        ch[i] = '';
      }
      out += ch[i] + 'S^' + String(exp);
    }
  }
  return out;
}
const makeZn = () => {
  zn = props.data.data.params.zn.split(' ')
  let out = '';
  for (let i = 0; i < zn.length; i++) {
    if (zn[i][0] != '-' && i != 0) {
      zn[i] = '+' + zn[i];
    }
    let exp = zn.length - 1 - i;
    if (exp == 0) {
      out += zn[i];
    }
    if (exp == 1) {
      if (zn[i] == '1') {
        zn[i] = '';
      }
      out += zn[i] + 'S';
    }
    if (exp > 1) {
      if (zn[i] == '1') {
        zn[i] = '';
      }
      out += zn[i] + 'S^' + String(exp);
    }

  }
  return out;
}

</script>
<template>
  <div class="container">
    <Handle id="in" type="target" :position="Position.Left" class="handle" />
    <div class="frame">
      <vue-latex :expression="'\\' + fnc + ''" :display-mode="true" />
    </div>
    <Handle id="out" type="source" :position="Position.Right" class="handle" />
  </div>
</template>
<style scoped>
/*transfer function*/
.frame {
  border: 1px solid black;
  padding-left: 5px;
  padding-right: 5px;
}

.handle {
  width: 6px;
  height: 6px;
  border: 1px solid black;
  border-radius: 3px;
}

.container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
</style>