import { Edge, Node, NodeDimensionChange } from "@braks/vue-flow";
import { Signal, Constant } from "@/components/types";
import { toRaw, isReactive, reactive } from "vue";
import { Mathamatic } from "./Math";
import {
  SOURCE,
  SCOPES,
  CONTINIOUS,
  MATH,
  SIN_VALUE, SIN_TITLE,
  STEP_VALUE, STEP_TITLE,
  CONST_VALUE, CONST_TITLE,
  SCOPE_VALUE, SCOPE_TITLE,
  DISPLAY_VALUE, DISPLAY_TITLE,
  TRANSER_FNC_VALUE, TRANSER_FNC_TITLE,
  INTEGRATOR_VALUE, INTEGRATOR_TITLE,
  ADD_VALUE, ADD_TITLE,
  SUB_VALUE, SUB_TITLE,
  PRODUCT_VALUE, PRODUCT_TITLE,
  DIV_VALUE, DIV_TITLE,
  DIFERECIATOR_VALUE, GAIN_VALUE
} from "@/components/values";
//в handles B-вход, A-выход
export class Runner {
  //массивы рассортированных блоков
  sources: Node[] = []; //источники
  process: Node[] = []; //промежуточные блоки
  scopes: Node[] = []; //конечные блоки
  //узлы и соедиения
  nodes: Node[] = [];
  edges: Edge[] = [];
  //время
  time: number;
  //текущий индекс массива дискретизации
  index = 1 as number;
  //настройка симуляции
  readonly simulation_step: number = 0.01;
  //текущее время симудяции
  _time: number=this.simulation_step;
  //конструктор
  constructor(nodes: Node[], edges: Edge[], time: number) {
    //на вход принимаем
    this.time = time;
    this.getFromProxy(nodes, edges);
  }
  //объекты в конструкторе поступают в виде proxy
  //этот метод из разворачивает proxy и записывает обычные объекты в nodes[]
  getFromProxy(nodes: Node[], edges: Edge[]) {
    for (let i = 0; i < nodes.length; i++) {
      const temp: Node = toRaw(nodes[i]);
      //поле data отдельно завернуто в proxy
      temp.data = toRaw(temp.data);
      this.nodes[i] = temp;
    }
    for (let i = 0; i < edges.length; i++) {
      const temp: Edge = toRaw(edges[i]);
      temp.data = toRaw(temp.data);
      this.edges.push(temp);
    }
  }
  //метод для обнуления результатов предыдущей симуляции
  preProcess(): void {
    for (let i = 0; i < this.nodes.length; i++) {
      this.nodes[i].data.input_signal = [];
      this.nodes[i].data.input_signal.push(
        {
          value: 0, time: '0'
        }as Signal
      )
      this.nodes[i].data.signal_output = [];
      this.nodes[i].data.signal_output.push(
        {
          value: 0, time: '0'
        }as Signal
      )
    }
  }
  //основной метод класса
  //запускает расчет схемы
  execute() {
    //обнуляем результаты предыдущей симуляции
    this.preProcess();
    //сортируем блоки на категории источников, обработчиков и блоков отображения
    this.sortBlocks();
    console.log(this.sources)
    this.Process();
    //this.postProcess();
    return this.nodes;
  }
  //определение типа блока
  getType(value: string): any {
    switch (value) {
      case SIN_VALUE: {
        return SOURCE;
        break;
      }
      case STEP_VALUE: {
        return SOURCE;
        break;
      }
      case CONST_VALUE: {
        return SOURCE;
        break;
      }
      case SCOPE_VALUE: {
        return SCOPES;
        break;
      }
      case DISPLAY_VALUE: {
        return SCOPES;
        break;
      }
      case TRANSER_FNC_VALUE: {
        return CONTINIOUS;
        break;
      }
      case INTEGRATOR_VALUE: {
        return CONTINIOUS;
        break;
      }
      case DIFERECIATOR_VALUE:{
        return CONTINIOUS;
        break;
      }
      case GAIN_VALUE:{
        return CONTINIOUS;
        break;
      }
      case ADD_VALUE: {
        return MATH;
        break;
      }
      case SUB_VALUE: {
        return MATH;
        break;
      }
      case PRODUCT_VALUE: {
        return MATH;
        break;
      }
      case DIV_VALUE: {
        return MATH;
        break;
      }
    }
  }
  //сортировка блоков на источники, промежуточные и конечные
  sortBlocks(): void {
    for (let i = 0; i < this.nodes.length; i++) {
      if (this.getType(this.nodes[i].data.value) == SOURCE) {
        this.sources.push(this.nodes[i]);
      }
      if (this.getType(this.nodes[i].data.value) == SCOPES) {
        this.scopes.push(this.nodes[i]);
      }
      if (
        this.getType(this.nodes[i].data.value) == CONTINIOUS ||
        this.getType(this.nodes[i].data.value) == MATH
      ) {
        this.process.push(this.nodes[i]);
      }
    }
  }
  //процесс
  Process() {
    while (this._time <= this.time) {
      for (let i = 0; i < this.sources.length; i++) {
        this.getSignalWay(this.sources[i], this.index, {time: this._time.toFixed(2), value: this.getBlockValueProcess(this.nodes[i], this.index)}as Signal);
      }
      this._time+=this.simulation_step;
      this.index++;
    }
  }
  //рекурсивный поиск пути сигнала
  getSignalWay(node: any, index:number, last_signal:Signal){
      this.nodes[this.getNodeIndex(node.id)].data.input_signal.push(last_signal);
      this.nodes[this.getNodeIndex(node.id)].data.signal_output.push({value: this.getBlockValueProcess(node, this.index), time: this._time.toFixed(2)} as Signal);
      const current_edges = this.getEdgesFromSource(node.id);
      if(current_edges.length==0){
        return;
      }
      for(let i=0; i<current_edges.length; i++){
        const current_node = this.getNodeFromTarget(current_edges[i].target); //получаем блок по концу стрелки
        //если узел нашелся, то проверяем, является ли он замыкающим в контуре
        //механизм обнаружения петлей
        //if (
        //    this.nodes[this.getNodeIndex(current_node.id)].data.input_signal.length-1>=index) {
        this.getSignalWay(current_node, index, 
          this.nodes[this.getNodeIndex(node.id)].data
            .signal_output[index])
     // } 
  }
}
  //получение узла по назначению
  getNodeFromTarget(target: string) {
    let answer: any = -1;
    for (let i = 0; i < this.nodes.length; i++) {
      if (this.nodes[i].id == target) {
        answer = this.nodes[i];
      }
    }
    return answer;
  }
  //получение стрелки
  getEdgesFromSource(source: string) {
    // eslint-disable-next-line
    let answer = [];
    for (let i = 0; i < this.edges.length; i++) { 
      if (this.edges[i].source == source) {
        answer.push(this.edges[i]);
      }
    }
    return answer;
  }
  //получение индекса узла
  getNodeIndex(id: string) {
    let answer: any = -1;
    for (let i = 0; i < this.nodes.length; i++) {
      if (this.nodes[i].id == id) {
        answer = i;
      }
    }
    return answer;
  }
  //селектор для обработки  (на вход мы получаем узел)
  //node - обрабатываемый блок
  getBlockValueProcess(node: Node, index: number) {
    switch (node.data.value) {
      case SIN_VALUE: {
        return this.getSin(node, index);
        break;
      }
      case STEP_VALUE: {
        return this.getStep(node, index);
        break;
      }
      case CONST_VALUE: {
        return this.getConst(node);
        break;
      }
      case TRANSER_FNC_VALUE: {
        return this.getTransferFunction(node, index);
        break;
      }
      case INTEGRATOR_VALUE: {
        return this.getIntegrator(node, index);
        break;
      }
      case DIFERECIATOR_VALUE:{
        return this.getDifferential(node, index);
        break;
      }
      case GAIN_VALUE:{
        return this.getGain(node, index);
        break;
      }
      case ADD_VALUE: {
        return this.getSum(node, index);
        break;
      }
      case SUB_VALUE: {
        return this.getSub(node, index);
        break;
      }
      case PRODUCT_VALUE: {
        return this.getProd(node, index);
        break;
      }
      case DIV_VALUE: {
        return this.getDiv(node, index);
        break;
      }
    }
  }
  //обработчики блоков
  getConst(node: Node) {
    return node.data.params.amplitude;
  }
  getStep(node: Node, index: number) {
    let out;
    if (index*this.simulation_step <= node.data.params.time) {
      out = node.data.params.start_value;
    } else {
      out = node.data.params.stop_value;
    }
    return out;
  }
  getSin(node: Node, index: number) {
    return Mathamatic.Sin(
      node.data.params.amplitude,
      node.data.params.frequency,
      node.data.params.phase,
      index*this.simulation_step
    );
  }
  getTransferFunction(node: Node, index: number) {
    return 1;
  }
  getIntegrator(node: Node, index: number) {
    let out=(node.data.input_signal[index].value+node.data.input_signal[index-1].value)*0.5*this.simulation_step+node.data.signal_output[index-1].value;
    if(node.data.params.agree_limit && out>node.data.params.limit){
      out=node.data.params.limit;
    }
    if(node.data.params.reset && out>node.data.params.limit){
      out=node.data.params.start_value;
    }
    return out; 
  }
  getSum(node: Node, index: number) {
    return;
  }
  getSub(node: Node, index: number) {
    return;
  }
  getProd(node: Node, index: number) {
    return;
  }
  getDiv(node: Node, index: number) {
    return;
  }
  getDifferential(node: Node, index:number){
    return (node.data.input_signal[index].value-node.data.input_signal[index-1].value)/this.simulation_step;
  }
  getGain(node: Node, index:number){
    return node.data.input_signal[index].value*node.data.params.value;
  }
  //оборачивание узлов обратно в proxy
  postProcess() {
    for (let i = 0; i < this.nodes.length; i++) {
      this.nodes[i].data = reactive(this.nodes[i].data);
      this.nodes[i] = reactive(this.nodes[i]);
    }
  }
}
