<script setup>
import { Handle, Position } from '@braks/vue-flow'
import { onMounted, ref, computed, watch } from 'vue'
import ConstntBlock from '@/components/blocks/ConstntBlock.vue';
import SinBlock from '../blocks/SinBlock.vue';
import StepBlock from '../blocks/StepBlock.vue';
import DisplayBlock from '../blocks/DisplayBlock.vue';
import TransferFncBlock from '../blocks/TransferFncBlock.vue';
import IntegratorBlock from '../blocks/IntegratorBlock.vue';
import DerivativeBlock from '../blocks/DerivativeBlock.vue';
import {
  SIN_VALUE,
  STEP_VALUE,
  CONST_VALUE,
  SCOPE_VALUE,
  DISPLAY_VALUE,
  TRANSER_FNC_VALUE,
  INTEGRATOR_VALUE,
  ADD_VALUE,
  SUB_VALUE,
  PRODUCT_VALUE,
  DIV_VALUE,
  GAIN_VALUE,
  DIFERECIATOR_VALUE
} from "@/components/values";
import GainBlock from '../blocks/GainBlock.vue';
import ScopeBlock from '../blocks/ScopeBlock.vue';
import SumBlock from '../blocks/SumBlock.vue';
import SubBlock from '../blocks/SubBlock.vue';
import ProdBlock from '../blocks/ProdBlock.vue';
import DivBlock from '../blocks/DivBlock.vue';
const props = defineProps({
  data: {
    type: Object,
    required: true
  }
})
const emit = defineEmits(['delete', 'updateBlock'])
let icon_path = '';
//активируется при монтировании компонента
onMounted(() => {
  console.log('mounted')
})
//удаление блока
const deleteBlock = () => {
  emit('delete', props.data.id)
}
//изменение блока
const editBlock = () => {
  emit('updateBlock', props.data.id) 
}
</script>

<template>
  <div @click.prevent.middle.exact="deleteBlock" @click.prevent.right.exact="editBlock" @dblclick.prevent="editBlock">
    <ul class="navbar-nav parent">
      <li class="nav-item child">
        <!--шаблоны отрисовки превью блоков-->
        <ScopeBlock v-if="props.data.data.value == SCOPE_VALUE" :data="props.data"/>
        <DisplayBlock v-if="props.data.data.value == DISPLAY_VALUE" :data="props.data"/>
        <TransferFncBlock v-if="props.data.data.value == TRANSER_FNC_VALUE" :data="props.data"/>
        <StepBlock v-if="props.data.data.value == STEP_VALUE" :data="props.data"/>
        <SinBlock v-if="props.data.data.value == SIN_VALUE" :data="props.data"/>
        <ConstntBlock v-if="props.data.data.value == CONST_VALUE" :data="props.data"/>
        <GainBlock v-if="props.data.data.value == GAIN_VALUE" :data="props.data"/>
        <IntegratorBlock v-if="props.data.data.value == INTEGRATOR_VALUE" :data="props.data"/>
        <DerivativeBlock v-if="props.data.data.value == DIFERECIATOR_VALUE" :data="props.data"/>
        <SumBlock v-if="props.data.data.value == ADD_VALUE" :data="props.data"/>
        <SubBlock v-if="props.data.data.value == SUB_VALUE" :data="props.data"/>
        <ProdBlock v-if="props.data.data.value == PRODUCT_VALUE" :data="props.data"/>
        <DivBlock v-if="props.data.data.value == DIV_VALUE" :data="props.data"/>
      </li>
      <li class="nav-item">
        <p class="nav-link">{{ props.data.label }}</p>
      </li>
    </ul>
  </div>
</template>

